"use client"

import postService from "@/services/postService"
import { useRouter } from "next/navigation"
import { createContext, useContext, useState, useEffect } from "react"

// Sample cart data - in a real app, this would come from an API or localStorage
const initialCartItems = [
  {
    id: 1,
    name: "Wool Blend Coat",
    price: 129.99,
    quantity: 1,
    image: "/placeholder.svg?height=120&width=100",
    color: "Beige",
    size: "M",
  },
  {
    id: 2,
    name: "Cashmere Sweater",
    price: 89.99,
    quantity: 2,
    image: "/placeholder.svg?height=120&width=100",
    color: "Gray",
    size: "L",
  },
]

const CartContext = createContext(undefined)

export function CartProvider({ children }) {
  const [cartItems, setCartItems] = useState([])
  const [isLoaded, setIsLoaded] = useState(false)
  const [mycart, setmycart] = useState([]);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [user, setUser] = useState(null);
  const router = useRouter();

  useEffect(() => {
      const fetchMycart = async () => {
        try {
          const response = await postService.getMyCarts();
          setmycart(response.data.mycart);
          setError(null);
        } catch (error) {
          setError("No Cart items found.");
        }
      };
      fetchMycart();
    }, []);
  
    useEffect(() => {
      fetch("http://localhost:8001/api/checkAuth", {
        method: "GET",
        credentials: "include",
      })
        .then((res) => res.json())
        .then((data) => {
          if (data.isAuthenticated) {
            setUser({
              id: data.userId,
              username: data.username,
              email: data.email,
              profile: data.profile ? data.profile : null,
            });
          }
        });
    }, [router]);

  // Initialize cart on client-side only
  useEffect(() => {
    // In a real app, you would fetch from localStorage or an API
    setCartItems(initialCartItems)
    setIsLoaded(true)
  }, [])

  // Get total number of items in cart
  const cartCount = mycart.length;

  // Add item to cart
  const addToCart = (product, quantity = 1) => {
    setCartItems((prevItems) => {
      const existingItem = prevItems.find((item) => item.id === product.id)

      if (existingItem) {
        // Update quantity if item already exists
        return prevItems.map((item) =>
          item.id === product.id ? { ...item, quantity: item.quantity + quantity } : item,
        )
      } else {
        // Add new item
        return [...prevItems, { ...product, quantity }]
      }
    })
  }

  // Remove item from cart
  const removeFromCart = (productId) => {
    setCartItems((prevItems) => prevItems.filter((item) => item.id !== productId))
  }

  // Update item quantity
  const updateQuantity = (productId, quantity) => {
    if (quantity < 1) return

    setCartItems((prevItems) => prevItems.map((item) => (item.id === productId ? { ...item, quantity } : item)))
  }

  // Clear cart
  const clearCart = () => {
    setCartItems([])
  }

  return (
    <CartContext.Provider
      value={{
        cartItems,
        cartCount,
        addToCart,
        removeFromCart,
        updateQuantity,
        clearCart,
        isLoaded,
      }}
    >
      {children}
    </CartContext.Provider>
  )
}

export function useCart() {
  const context = useContext(CartContext)

  if (context === undefined) {
    throw new Error("useCart must be used within a CartProvider")
  }

  return context
}

